<template>
  <div class="matching">
    <div class="header">
      <div class="selectNav">
        <el-button type="primary" size="small" @click="handleAdd" style="float: left">新增</el-button>
        <el-input class="input_300" placeholder="标签名称" size="small" v-model="tagName" clearable></el-input>
        <el-button type="primary" size="small" icon="el-icon-search" @click="GetQdRcList">查询</el-button>
        <el-button type="primary" size="small" @click="setQzghData">核验数据</el-button><!--潜在过户、渠道定制化-->
      </div>
    </div>
    <div class="content">
      <div class="title">渠道偏好引流</div>
      <div class="tableTd">
        <el-table
          v-loading="loading"
          border
          :data="tableData"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column label="序号" align="center" type="index" width="70"></el-table-column>
          <el-table-column prop="tagName" label="标签名称"></el-table-column>
          <el-table-column prop="collect" width="120" label="覆盖客户数">
            <template slot-scope="scope">
              <el-link type="primary" @click="handleDetails(scope.row,0)">{{scope.row.collect}}</el-link>
            </template>
          </el-table-column>
          <el-table-column prop="signCount" width="120" label="手工标记客户数">
            <template slot-scope="scope">
              <el-link type="primary" @click="handleDetails(scope.row,1)">{{scope.row.signCount}}</el-link>
            </template>
          </el-table-column>
          <el-table-column prop="userName" label="创建人"></el-table-column>
          <el-table-column prop="createTime" label="创建时间"></el-table-column>
          <el-table-column prop="updateName" label="修改人"></el-table-column>
          <el-table-column prop="updateTime" label="修改时间"></el-table-column>
          <el-table-column align="center" width="160" label="操作">
            <template slot-scope="scope">
              <el-link type="primary" @click="handleEdit(scope.row)">修改</el-link>
              <el-divider direction="vertical"></el-divider>
              <el-link type="danger" @click="DeleteMessageSendTask(scope.row)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pages.pageNo"
            :page-sizes="[8, 10, 20, 50]"
            :page-size="pages.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pages.total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!--        //新增-->
    <div v-if="successListVisible">
      <el-dialog
        class="successList-dialog"
        :visible.sync="successListVisible"
        width="90%"
        top="5vh"
        :before-close="successListHandleClose"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
      >
        <span slot="title" class="dialog-header">
          <!--                <span class="header-text">{{ '渠道偏好引流' }}</span>-->
          <el-button
            type="primary"
            size="small"
            @click="saveDate"
            style="float: right;margin-right: 40px"
          >保存</el-button>
        </span>
        <div class="dialog-content">
          <div class="addLeft">
            <el-input
              style="width: calc(100% - 66px)"
              placeholder="输入关键字进行过滤"
              size="medium"
              v-model="filterText2"
            ></el-input>
            <el-button @click="selectTree(filterText2)" icon="el-icon-search" size="medium"></el-button>
            <!--                     default-expand-all-->
            <el-tree
              :style="treeStyleObject"
              v-loading="treeLoading"
              v-show="!showSelectTree"
              class="filter-tree"
              :filter-node-method="filterNode"
              ref="tree"
              :props="defaultProps"
              :load="loadNode"
              :highlight-current="true"
              :expand-on-click-node="false"
              @node-click="nodeClick"
              lazy
            >
              <span class="custom-tree-node" slot-scope="{ node, data }">
                <span>
                  <i v-if="data.type==='tagobj'" class="el-icon-office-building"></i>
                  <i v-else-if="data.type==='index'" class="el-icon-folder-opened"></i>
                  <i v-else-if="data.type==='tag'" class="el-icon-price-tag"></i>
                  {{ node.label }}
                </span>
              </span>
            </el-tree>
            <el-tree
              :style="{treeStyleObject}"
              v-loading="treeLoading"
              v-show="showSelectTree"
              :data="dataTree"
              default-expand-all
              :props="defaultProps2"
              :expand-on-click-node="false"
              @node-click="nodeClick"
              :highlight-current="true"
            >
              <span class="custom-tree-node" slot-scope="{ node, data }">
                <span>
                  <i v-if="data.type==='tagobj'" class="el-icon-office-building"></i>
                  <i v-else-if="data.type==='index'" class="el-icon-folder-opened"></i>
                  <i v-else-if="data.type==='tag'" class="el-icon-price-tag"></i>
                  {{ node.label }}
                </span>
              </span>
            </el-tree>
          </div>
          <div class="addRight"><!--网格化定制化-->
            <div class="query" v-if="$route.query.mark==='wgh'">
              <span class="text">地市局：</span>
              <el-select
                  class="input"
                  v-model="municipalBureau"
                  size="mini"
                  clearable
                  placeholder="请选择"
                  @change="dsjChange"
              >
                <el-option
                    v-for="item in municipalBureauList"
                    :key="item.ORG_CODE"
                    :label="item.ORG_NAME"
                    :value="item.ORG_CODE"
                ></el-option>
              </el-select>
              <span class="text">县区局：</span>
              <el-select
                  class="input"
                  v-model="districtBureau"
                  size="mini"
                  clearable
                  placeholder="请选择"
                  @change="qxjChange"
              >
                <el-option
                    v-for="item in districtBureauList"
                    :key="item.ORG_CODE"
                    :label="item.ORG_NAME"
                    :value="item.ORG_CODE"
                ></el-option>
              </el-select>
              <span class="text">供电所：</span>
              <el-select class="input" v-model="powerSubstation" size="mini" clearable placeholder="请选择">
                <el-option
                    v-for="item in powerSubstationList"
                    :key="item.ORG_CODE"
                    :label="item.ORG_NAME"
                    :value="item.ORG_CODE"
                ></el-option>
              </el-select>
              <el-button type="primary" size="mini" icon="el-icon-search" @click="getTagByCustomers">查询</el-button>
              <el-button type="primary" size="mini"  @click="changeSelect">{{allSelect?'取消全选':'全选'}}</el-button>
            </div>
            <el-table :data="tableData2" v-loading="loadingAddTable" border style="width: 100%">
              <el-table-column type="index" label="序号" width="70"></el-table-column>
              <el-table-column prop="yhbh" label="客户编号"></el-table-column>
              <el-table-column prop="yhmc" label="客户名称"></el-table-column>
              <el-table-column prop="yhlb" label="用户类型" width="240"></el-table-column>
              <el-table-column prop="ydlx" label="用电类别"></el-table-column>
              <el-table-column prop="dq" label="地区"></el-table-column>
              <el-table-column prop="gddw" label="供电所"></el-table-column>
              <el-table-column label="手工标记" width="140">
                <template slot-scope="scope">
                  <el-checkbox v-model="scope.row.issign" @change="handleCheckbox(scope.row)"></el-checkbox>
                </template>
              </el-table-column>
            </el-table>
            <div class="block">
              <el-pagination
                @size-change="handleAddSizeChange"
                @current-change="handleAddCurrentChange"
                :current-page="pagesAdd.pageNo"
                :page-sizes="[8, 10, 20, 50]"
                :page-size="pagesAdd.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagesAdd.total"
              ></el-pagination>
            </div>
          </div>
        </div>
      </el-dialog>
    </div>

    <!--        //客户数详情-->
    <el-dialog
      class="successList-dialog"
      :visible.sync="khNoShow"
      width="90%"
      top="5vh"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '客户详情' }}</span>
      </span>
      <div class="dialog-content" v-loading="loadingDetails">
        <div class="selectNav">
          <el-input class="input_300" placeholder="客户名称" size="small" v-model="yhmcDet" clearable></el-input>
          <el-input class="input_300" placeholder="客户编号" size="small" v-model="yhbhDet" clearable></el-input>
          <el-button
            type="primary"
            size="small"
            icon="el-icon-search"
            @click="handleDetails(detRow,2)"
          >查询</el-button>
          <el-button
            type="primary"
            size="small"
            icon="el-icon-search"
            @click="exportExc"
            :loading="excelLoading"
          >导出excel</el-button>
        </div>
        <div style="height: 500px">
          <bdaTable
            :tableData="successListData"
            :isPage="true"
            :height="500"
            :pageConfig="successListPageConfig"
            @changePage="changePage"
          ></bdaTable>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Interface from "@/base/mixin/channel-preference";
import Interface2 from "@/base/mixin/common-api";
import bdaTable from "@/components/bda-table/bda-table";
import { CP_PATH, QD_TAG_OBJ } from "@/config";
import axios from "axios";
// import {}
export default {
  name: "matching",
  mixins: [Interface, Interface2],
  components: { bdaTable },
  props: {},
  data() {
    return {
      /**网格化经理添加地市局查询模块**/
      //当前等级
      orgLev: "",
      //地市局
      municipalBureau: "",
      municipalBureauList: [],
      //县区局
      districtBureau: "",
      districtBureauList: [],
      //供电所
      powerSubstation: "",
      powerSubstationList: [],
      powerSupplyCode:'',
      allSelect:true,//默认全选
      /**网格化经理添加地市局查询模块**/

      //导航模块变量
      tagName: "",
      //首页表格
      pages: {
        pageNo: 1,
        pageSize: 10,
        total: 0
      },
      tableData: [],
      loading: false,
      homeTableId: "",
      //详情弹窗
      successListVisible: false,
      successListData: {},
      successListQueryForm: {
        current: 1,
        size: 10
      },
      successListPageConfig: {
        name: "successList",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      khNoShow: false,
      loadingDetails: false,
      yhbhDet: "",
      yhmcDet: "",
      detRow: {},

      //新增
      // 标签树
      defaultProps: {
        children: "children",
        label: "text",
        isLeaf: "leaf"
      },
      defaultProps2: {
        children: "children",
        label: "text"
      },
      filterText2: "",
      treeLoading: false,
      showSelectTree: false,
      dataTree: [],

      node: [],
      resolve: [],
      numb: 1,
      treeStyleObject: {
        maxHeight: 560 + "px",
        minHeight: 300 + "px",
        overflow: "scroll"
      },

      //标签树查询表格数据
      tagId: "", //选中标签id
      tagNameSelect: "", //选中标签名称
      loadingAddTable: false,
      pagesAdd: {
        pageNo: 1,
        pageSize: 10,
        total: 0
      },
      tableData2: [],
      coderList: [], //取消标签编号集合
      titleDw: "新增", //新增或者修改
      excelLoading: false,

      markData: null
    };
  },
  watch: {
    tagId(val, oldVal) {
      //普通的watch监听
      console.log("a: " + val, oldVal);
      this.coderList = [];
    }
  },
  mounted() {
    this.GetPowerAreaCode();
    window.console.log(QD_TAG_OBJ);
    this.GetQdRcList();
  },
  methods: {
    handleSizeChange(val) {
      this.pages.pageSize = val;
      this.GetQdRcList();
      window.console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.pages.pageNo = val;
      this.GetQdRcList();
      window.console.log(`当前页: ${val}`);
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return "success-row";
      }
      return "";
    },

    //获取推荐
    GetQdRcList() {
      this.loading = true;
      this.getQdRcList({
        tagName: this.tagName,
        size: this.pages.pageSize,
        current: this.pages.pageNo
      }).then(res => {
        this.loading = false;
        this.pages.total = res.total;
        this.tableData = res.records;
      });
    },

    /**
     * //客户详情
     * **/
    //改变分页
    changePage(data) {
      if (data.name === "successList") {
        this.successListQueryForm.size = data.size;
        this.successListQueryForm.current = data.current;
        this.handleDetails(this.detRow, 3);
      }
    },
    exportExc2() {
      // window.location.href = CP_PATH+'/qdtj/downLoad?id='+this.detRow.id+'&tagId='+this.detRow.tagId+'&yhmc='+this.yhmcDet+'&yhbh='+this.yhbhDet
      try {
        window.location.href =
          CP_PATH +
          "/qdtj/downLoad?id=" +
          this.detRow.id +
          "&tagId=" +
          this.detRow.tagId +
          "&yhmc=" +
          this.yhmcDet +
          "&yhbh=" +
          this.yhbhDet;
      } finally {
        this.excelLoading = false;
      }
    },
    //下面这个方法备用
    exportExc() {
      this.excelLoading = true;
      let path = "/qdtj/downLoad";
      let data = {
        id: this.detRow.id,
        tagId: this.detRow.tagId,
        yhmc: this.yhmcDet,
        yhbh: this.yhbhDet,
        mark: this.markData
      };
      axios({
        method: "get",
        url: CP_PATH + path,
        params: data,
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        responseType: "blob"
      })
        .then(res => {
          console.log(res, "res");

          const content = res.data;
          const blob = new Blob([content]);
          const fileName = "导出信息.xls";
          if ("download" in document.createElement("a")) {
            // 非IE下载
            const elink = document.createElement("a");
            elink.download = fileName;
            elink.style.display = "none";
            elink.href = URL.createObjectURL(blob);
            document.body.appendChild(elink);
            elink.click();
            URL.revokeObjectURL(elink.href); // 释放URL 对象
            document.body.removeChild(elink);
          } else {
            // IE10+下载
            navigator.msSaveBlob(blob, fileName);
          }
        })
        .finally(() => {
          this.excelLoading = false;
        })
        .catch(() => {
          this.excelLoading = false;
        });
    },

    //详情弹窗
    handleDetails(row, tp = 0) {
      //tp==0刚刚进来时查询覆盖客户 tp===1手工  、==2点击查询 ==3分页查询
      if (tp === 0 || tp === 1) {
        //首页进来清空查询条件
        this.yhmcDet = "";
        this.yhbhDet = "";
        this.successListQueryForm.current = 1; //进来默认分页变为第一页
        this.markData = tp;
      } else if (tp === 2) {
        //查询后分页变为第一页
        this.successListQueryForm.current = 1;
      }
      this.detRow = row;
      this.khNoShow = true;
      this.loadingDetails = true;
      let columnNameData = [
        { prop: "yhbh", label: "客户编号" },
        { prop: "yhmc", label: "客户名称" },
        { prop: "yddz", label: "用电地址" },
        { prop: "yhlb", label: "用户类型" },
        { prop: "ydlx", label: "用电类别" },
        { prop: "lxr", label: "联系人" },
        { prop: "lxdh", label: "联系电话", width: 120 },
        { prop: "dq", label: "地区" },
        { prop: "gddw", label: "供电所" },
        { prop: "sign", label: "是否手工标记" }
      ];
      this.getQdRcDeail({
        id: this.detRow.id,
        tagId: this.detRow.tagId,
        yhmc: this.yhmcDet,
        yhbh: this.yhbhDet,
        mark: this.markData,
        // "id":"347463951212085248",
        // "tagId":"10014",
        // "yhmc":"奥利奥",
        // "yhbh":"0318009900099282",
        current: this.successListQueryForm.current,
        size: this.successListQueryForm.size
      }).then(res => {
        this.loadingDetails = false;
        this.successListPageConfig.total = res.total;
        this.successListPageConfig.size = this.successListQueryForm.size;
        (this.successListPageConfig.current = this.successListQueryForm.current),
          (this.successListData = {
            tableData: [...res.records],
            headerColor: "#1899fa",
            title: "详情",
            name: "successList",
            size: this.successListPageConfig.size,
            current: this.successListPageConfig.current,
            total: res.total,
            columnNameData
          });
      });
    },
    //删除
    DeleteMessageSendTask(row) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteQdRcList({
            id: row.id
          }).then(() => {
            this.GetQdRcList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    // 关闭成功
    successListHandleClose() {
      this.successListVisible = false;
    },
    // 新增弹窗
    handleAdd() {
      this.titleDw = "新增";
      this.successListVisible = true;
      this.filterText2 = "";
      this.tableData2 = [];
      this.coderList = [];
    },
    // 修改弹窗
    handleEdit(row) {
      this.filterText2 = "";
      this.tableData2 = [];
      this.coderList = [];
      this.$nextTick(() => {
        this.titleDw = "修改";
        this.homeTableId = row.id; //修改用
        this.successListVisible = true;
        this.filterText2 = row.tagName;
        this.tagNameSelect = row.tagName;
        this.tagId = row.tagId;
        this.coderList = row.yhbh.split(",");
        // this.selectTree(this.filterText2)
        this.tagId = row.tagId;
        this.getTagByCustomers();
      });
    },

    //标签相关方法
    //标签树
    selectTree(keywords) {
      this.treeLoading = true;
      let keywordTemp = "";
      if (keywords != "" && keywords != null) {
        keywordTemp = keywords;
        this.showSelectTree = true;
        this.value = "";
      } else {
        this.showSelectTree = false;
        keywordTemp = this.value;
        this.filterText2 = "";
      }
      this.node.childNodes = [];
      this.getBdaTagList({
        keyword: keywordTemp,
        id: 0
        // tagobjId: QD_TAG_OBJ,
      })
        .then(res => {
          this.dataTree = res;
          this.treeLoading = false;
          this.resolve(this.dataTree);
        })
        .catch(() => {});
      //手动调用加载数据方法
      // this.loadNode(this.node, this.resolve);
    },
    filterNode(value, data) {
      if (!value) return true;
      return data.text.indexOf(value) !== -1;
    },
    loadNode(node, resolve) {
      //加载数据的方法里把node，resolve存起来 ==== 只存初始化时的数据
      if (this.numb === 1) {
        this.node = node;
        this.resolve = resolve;
        // 把子节点清空，否则下次加载时会直接往里push子节点导致重复
        this.node.childNodes = [];
        this.numb++;
      }
      // let permissionArr =[]
      // permissionArr = ['1','4']

      this.dataTree = [];
      let $this = this;
      if (node.data) {
        if (node.data.type) {
          this.getBdaTagList({
            id: node.data.entryId,
            type: node.data.type
            // tagobjId: QD_TAG_OBJ,
            //filterStatus:[3,5,7], //[1,2,4,6],//过滤除了“入库3”、“停用5”、“下线7”的标签；
            // filterTagRuleTypes: [3, 4]//过滤其他标签，只要大数据平台  1==大数据平台，3==手工标记 ，4==人工智能平台
          })
            .then(res => {
              $this.dataTree = res;
              $this.dataTree.forEach(item => {
                if (item.type == "tag") {
                  item.leaf = true;
                }
              });
              this.treeLoading = false;
              resolve($this.dataTree);
            })
            .catch(() => {});
        } else {
          resolve([]);
        }
      } else {
        let params = {
          // tagobjId: QD_TAG_OBJ,
          // keyword:'11',
          //filterStatus: ["7"],//过滤下线标签
          // filterTagRuleTypes: qs.stringify( permissionArr,{ arrayFormat: "repeat" })//过滤其他标签，只要手工标签
        };
        this.treeLoading = true;
        this.getBdaTagList(params)
          .then(res => {
            $this.options = res;
            for (let i = 0; i < res.length; i++) {
              res[i].disabled = true;
            }
            this.dataTree = res;
            this.treeLoading = false;
            resolve(this.dataTree);
          })
          .catch(() => {});
      }
    },
    nodeClick(a) {
      if (a.type === "tag") {
        window.console.log(a);
        this.tagId = a.entryId;
        this.tagNameSelect = a.name;
        this.getTagByCustomers();
      }
      // 这里有三个参数
    },

    //右边表格相关方法
    getTagByCustomers() {
      this.loadingAddTable = true;
      this.tagByCustomers({
        tagId: this.tagId,
        size: this.pagesAdd.pageSize,
        current: this.pagesAdd.pageNo,
        powerSupplyCode: this.powerSubstation
            ? this.powerSubstation
            : this.districtBureau
            ? this.districtBureau
            : this.municipalBureau
      }).then(res => {
        this.tableData2 = res.records.filter(v => {
          return (v.issign = v.issign == "1" ? true : false);
        });
        window.console.log(this.coderList, "pppp");
        if (this.coderList.length > 0) {
          this.coderList.forEach(item => {
            this.tableData2.forEach(v => {
              if (v.yhbh == item) {
                v.issign = false;
              }
            });
          });
        }
        window.console.log(this.tableData2, "tableData2");
        this.pagesAdd.total = res.total;
        this.loadingAddTable = false;
      });
    },
    handleAddSizeChange(val) {
      this.pagesAdd.pageSize = val;
      this.getTagByCustomers();
      window.console.log(`每页 ${val} 条`);
    },
    handleAddCurrentChange(val) {
      this.pagesAdd.pageNo = val;
      this.getTagByCustomers();
      window.console.log(`当前页: ${val}`);
    },
    handleCheckbox(row) {
      if (!row.issign) {
        //取消标记
        if (this.coderList.indexOf(row.yhbh) == -1) {
          this.coderList.push(row.yhbh);
        }
      } else {
        //标记上
        if (this.coderList.length > 0) {
          this.coderList = this.coderList.filter(item => {
            return item != row.yhbh;
          });
        }
      }
    },

    changeSelect(){
      this.allSelect = !this.allSelect
      this.tableData2.forEach(v=>{
        this.allSelect?v.issign = true:v.issign = false
        if (!v.issign) {
          //取消标记
          if (this.coderList.indexOf(v.yhbh) == -1) {
            this.coderList.push(v.yhbh);
          }
        } else {
          //标记上
          if (this.coderList.length > 0) {
            this.coderList = this.coderList.filter(item => {
              return item != v.yhbh;
            });
          }
        }
      })

    },

    //保存
    saveDate() {
      let params = {
        tagId: this.tagId,
        tagName: this.tagNameSelect,
        yhbhs: this.coderList
      };
      if (this.titleDw === "修改") {
        params.id = this.homeTableId;
      }
      this.saveQdtj(params)
        .then(() => {})
        .finally(() => {
          this.successListVisible = false;
          this.GetQdRcList();
        });
    },

    /**网格化经理添加地市局查询模块**/
    //获取供电单位编码--地市局
    GetPowerAreaCode() {
      this.getPowerAreaCode({
        orgLev: "2" //2==供电单位==地市局，3==县区局局，4===供电所
        // "orgCode":"0306" //上一级编码
      }).then(res => {
        this.municipalBureauList = res;
      });
    },
    //获取区县局--区县局
    GetQxjPowerAreaCode() {
      this.getPowerAreaCode({
        orgLev: "3", //2==供电单位==地市局，3==县区局局，4===供电所
        orgCode: this.municipalBureau //上一级编码
      }).then(res => {
        this.districtBureauList = res;
      });
    },
    //获取供电所
    GetGdsPowerAreaCode() {
      this.getPowerAreaCode({
        orgLev: "4", //2==供电单位==地市局，3==县区局局，4===供电所
        orgCode: this.districtBureau //上一级编码
      }).then(res => {
        this.powerSubstationList = res;
      });
    },
    //地市局选择
    dsjChange() {
      this.districtBureau = "";
      this.districtBureauList = [];
      this.powerSubstation = "";
      this.powerSubstationList = [];
      this.GetQxjPowerAreaCode();
    },
    //区县局选择
    qxjChange() {
      this.powerSubstation = "";
      this.powerSubstationList = [];
      this.GetGdsPowerAreaCode();
    },
    //潜在过户定制化接口
    setQzghData(){
      if(this.$route.query.mark ==='qzgh'){  //潜在过户
        this.executeTask({}).then(()=>{
          this.GetQdRcList()
        })
      }else {//渠道定制化
        this.updateCustomersTotal({}).then(()=>{
          this.GetQdRcList()
        })
      }
    }
  }
};
</script>

